"use client";

import React from "react";
import ClickSpark from "../../../components/ClickSpark";
import TargetCursor from "../../../components/TargetCursor";
import AnimatedContent from "../../../components/AnimatedContent";
import SplitText from "../../../components/SplitText";
import { useRouter } from "next/navigation";

// Map slugs to PDF paths under public/
const PDF_MAP: Record<string, { title: string; pdfPath: string }> = {
  "ui-ux-design-professional": {
    title: "UI/UX Design Professional",
    pdfPath: "/certificates/great-learning-ui-ux-beginner-certificate.pdf",
  },
};

export default function CertificateViewer({ params }: { params: { slug: string } }) {
  const router = useRouter();
  const entry = PDF_MAP[params.slug];

  return (
    <ClickSpark sparkColor="#ffffff" sparkSize={10} sparkRadius={15} sparkCount={10} duration={450}>
      <main className="px-6 sm:px-10 py-10 mx-auto max-w-6xl">
        <TargetCursor spinDuration={2} hideDefaultCursor={true} />

        <section className="space-y-6">
          <AnimatedContent distance={80} direction="vertical" duration={1.0} initialOpacity={0} animateOpacity>
            <div className="mb-2 text-center">
              <SplitText
                text={entry ? entry.title : "Certificate"}
                className="text-2xl sm:text-3xl md:text-4xl font-extrabold tracking-tight"
                delay={120}
                duration={1.0}
                ease="power3.out"
                splitType="chars"
                from={{ opacity: 0, y: 30 }}
                to={{ opacity: 1, y: 0 }}
                threshold={0.1}
                rootMargin="-100px"
                textAlign="center"
              />
              <p className="mt-3 text-sm text-foreground/70">In-page PDF viewer</p>
            </div>
          </AnimatedContent>

          {!entry ? (
            <div className="p-6 rounded-xl border border-foreground/10 bg-background/50 backdrop-blur text-center">
              <p className="text-foreground/80">We couldn't find this certificate.</p>
              <button
                className="mt-4 inline-flex items-center gap-2 rounded-full bg-white/5 px-4 py-2 text-sm ring-1 ring-white/10 shadow transition hover:bg-white/10 cursor-target"
                onClick={() => router.back()}
              >
                ← Go back
              </button>
            </div>
          ) : (
            <AnimatedContent distance={60} direction="vertical" duration={0.8} initialOpacity={0} animateOpacity>
              <div className="rounded-xl border border-foreground/10 bg-background/30 overflow-hidden">
                {/* Use <object> with fallback to <iframe> for wider browser support */}
                <object data={entry.pdfPath} type="application/pdf" className="w-full" style={{ height: "80vh" }}>
                  <iframe src={entry.pdfPath} className="w-full" style={{ height: "80vh" }} title={entry.title} />
                </object>
              </div>
              <div className="mt-4 flex items-center justify-center gap-3">
                <a
                  href={entry.pdfPath}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="group inline-flex items-center gap-2 rounded-full bg-white/5 px-5 py-2.5 text-sm ring-1 ring-white/10 shadow transition hover:bg-white/10 cursor-target"
                >
                  Open in new tab →
                </a>
                <a
                  href={entry.pdfPath}
                  download
                  className="group inline-flex items-center gap-2 rounded-full bg-white/5 px-5 py-2.5 text-sm ring-1 ring-white/10 shadow transition hover:bg-white/10 cursor-target"
                >
                  Download PDF ↓
                </a>
              </div>
            </AnimatedContent>
          )}
        </section>
      </main>
    </ClickSpark>
  );
}
